.detail {
  padding: 0.2em;
}
.description {
  white-space: pre-wrap;
}

.iframecontainer {
  position: relative;
  padding-bottom: 56.25%; /* 16/9 ratio */
  padding-top: 30px; /* IE6 workaround*/
  height: 0;
  overflow: hidden;
  margin: 0;
}

.iframecontainer iframe,
.iframecontainer object,
.iframecontainer embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
